import React from "react"
import { projects } from "./projects"
import { FaEnvelope, FaLinkedin, FaPhone } from "react-icons/fa"

const Contact = () => {
    const icons = [FaEnvelope, FaLinkedin, FaPhone]
    const icon_name = ["Mail", "Linkedin", "+91 7024466626"]

    const contact_info = (item, key) => {
        let Icon = icons[key]
        return (<div className="contacts">
            <div className="icon_section"><span><Icon /></span></div>
            <span>{item}</span>
        </div>)
    }
    return (
        <div>
            <div className="Contact_info">
                {
                    icon_name.map((item, key) => {
                        return contact_info(item, key)
                    })
                }
            </div>

            <div className="footer">
            <div className=" foot foot1">
                    <span><b>PROJECTS</b></span>
                    <br />
                    <ul>
                        {
                            projects.map((value) => {
                                return <li><a href={value.link}>{value.name}</a></li>
                            })
                        }
                    </ul>
                </div>

                <div className=" foot foot2">
                    <span><b>CONTACT</b></span>
                    <br />
                    <ul>
                        <li><a href="mailto:lucky9755jain@gmail.com">Mail</a></li>
                    </ul>
                </div>

                <div className=" foot foot3">
                    <span><b>SKILLS</b></span>
                    <br />
                    <li><span>HTML</span></li>
                    <li><span>CSS</span></li>
                    <li><span>JavaScript</span></li>
                    <li><span>React</span></li>
                </div>

                <div className=" foot foot4">
                    <span><b>ABOUT</b></span>
                    <br />
                    <li><span>Pursuing BE (2019-2023)</span></li>

                </div>
            </div>
        </div>
    )
}

export default Contact